import React from 'react';
import { Row, Col, Tree, Form, Input, Button, Switch, InputNumber, message, Tag, TreeSelect, Icon,Popconfirm } from 'antd';
import { getAllMenu, saveMenu, delMenu } from 'api';
import PermissionContainer from 'permission';
import Icons from '@/Icons';
import "./menuStyle.less";

const TreeNode = Tree.TreeNode;
const FormItem = Form.Item;
const SelectTreeNode = TreeSelect.TreeNode;

const iconsTree = [];
for (let item of Icons) {
    let chidren = [];
    for (let child of item.icons) {
        chidren.push(<SelectTreeNode value={child.icon} title={<span><Icon type={child.icon} style={{ color: '#08c' }} /> &nbsp;&nbsp;{child.title}</span>} key={child.icon} />)
    }
    iconsTree.push(<SelectTreeNode selectable={false} title={<span>{item.title}</span>} key={item.title}>{chidren}</SelectTreeNode>)
}
class Menu extends React.PureComponent {
    state = {
        menuList: [],
        tempMenu: {
            id: '',
            parentId: 0
        },
        selected: false,
        addChild: false
    }
    componentDidMount() {
        this.initData();
    }
    initData = async () => {
        let menuListRes = await getAllMenu();
        let menuList = menuListRes.data;
        this.setState({
            menuList: menuList
        })
    }

    onSelect = (selectedKeys, info) => {
        let { setFieldsValue, resetFields } = this.props.form;
        if (selectedKeys.length === 0) {
            resetFields();
            this.setState({
                selected: false,
                addChild: false,
                tempMenu: {
                    id: '',
                    parentId: 0
                }
            })
            return;
        }
        let id = selectedKeys[0];
        let menu = this.findMenuById(id);
        this.setState({
            selected: true,
            addChild: false,
            tempMenu: { ...menu }
        });
        setFieldsValue({
            name: menu.name,
            title: menu.title,
            functionCode: menu.functionCode,
            sort: menu.sort,
            leftMemu: menu.leftMemu,
            isLock: menu.isLock,
            icon: menu.icon
        });
    }
    findMenuById = (id) => {
        let menu = {};
        let getMenu = function (menuList) {
            for (var item of menuList) {
                if (item.id == id) {
                    menu = { ...item };
                    menu.children = null;
                    break;
                } else {
                    if (item.children && item.children.length > 0) {
                        getMenu(item.children);
                    }
                }
            }
        };
        getMenu(this.state.menuList);
        return menu;
    }

    handleSubmit = (e) => {
        e.preventDefault();
        this.props.form.validateFieldsAndScroll(async (err, values) => {
            if (!err) {
                let data = { id: this.state.tempMenu.id, parentId: this.state.tempMenu.parentId, ...values };
                try {
                    await saveMenu(data);
                    message.success('');
                    this.initData();
                } catch (ex) {
                    console.log(ex);
                }

            }
        });
    }

    addChildMenu = () => {
        this.setState({
            addChild: true,
            selected: false,
            tempMenu: {
                ...this.state.tempMenu,
                id: '',
                parentId: this.state.tempMenu.id
            }
        });
        this.props.form.resetFields();
    }

    addTopMenu = () => {
        this.setState({
            addChild: false,
            selected: false,
            tempMenu: {
                ...this.state.tempMenu,
                id: '',
                parentId: 0
            }
        });
        this.props.form.resetFields();
    }

    resetFields = () => {
        this.props.form.resetFields()
    }

    removeMenu = () => {
        let id = this.state.tempMenu.id;
        delMenu({id}).finally(this.initData);
    }

    render() {
        const renderMenu = (menuList) => menuList.map(
            menu =>
                <TreeNode title={menu.title} key={menu.id}>
                    {menu.children && menu.children.length > 0 ? renderMenu(menu.children) : ''}
                </TreeNode>
        )
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 8 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 16 },
            },
        };
        const tailFormItemLayout = {
            wrapperCol: {
                xs: {
                    span: 24,
                    offset: 0,
                },
                sm: {
                    span: 16,
                    offset: 8,
                },
            },
        };
        return (
            <div>
                <Row type="flex" justify="start">
                    <Col xs={24} sm={24} md={12} lg={6} xl={6} style={{ backgroundColor: '#fafafa' }}>
                        <div style={{ padding: 10 }}>
                            <Button icon='plus' type="primary" size='small' onClick={this.addTopMenu}></Button>&nbsp;&nbsp;&nbsp;&nbsp;

                            <Button disabled={!this.state.selected} icon='plus' size='small' onClick={this.addChildMenu}></Button>
                        </div>
                        <Tree
                            onSelect={this.onSelect}
                        >
                            {renderMenu(this.state.menuList)}
                        </Tree>

                    </Col>
                    <Col xs={24} sm={24} md={12} lg={9} xl={9}>

                        <Form onSubmit={this.handleSubmit}>
                            <div style={{ padding: 10, paddingLeft: 50, display: this.state.selected ? 'block' : 'none' }}>
                                <Tag color="#108ee9">{this.state.tempMenu.title}</Tag>
                            </div>
                            <div style={{ padding: 10, paddingLeft: 50, display: this.state.addChild ? 'block' : 'none' }}>
                                &nbsp;&nbsp;<Tag color="#108ee9">{this.state.tempMenu.title}</Tag>
                            </div>
                            <FormItem
                                {...formItemLayout}
                                hasFeedback
                                label=""
                            >
                                {getFieldDecorator('name', {
                                    rules: [{
                                        required: true, message: '!',
                                    }],
                                })(
                                    <Input />
                                )}
                            </FormItem>
                            <FormItem
                                {...formItemLayout}
                                hasFeedback
                                label=""
                            >
                                {getFieldDecorator('title', {
                                    rules: [{
                                        required: true, message: '!',
                                    }],
                                })(
                                    <Input />
                                )}
                            </FormItem>
                            <FormItem
                                {...formItemLayout}
                                hasFeedback
                                label=""
                            >
                                {getFieldDecorator('functionCode')(
                                    <Input />
                                )}
                            </FormItem>
                            <FormItem
                                {...formItemLayout}

                                label=""
                            >
                                {getFieldDecorator('sort', { initialValue: 0 })(
                                    <InputNumber min={0} />
                                )}
                            </FormItem>
                            <FormItem
                                {...formItemLayout}

                                label=""
                            >
                                {getFieldDecorator('leftMemu', { valuePropName: 'checked' })(
                                    <Switch />
                                )}
                            </FormItem>
                            <FormItem
                                {...formItemLayout}

                                label=""
                            >
                                {getFieldDecorator('isLock', { valuePropName: 'checked' })(
                                    <Switch />
                                )}
                            </FormItem>
                            <FormItem
                                {...formItemLayout}
                                hasFeedback
                                label=""
                            >
                                {getFieldDecorator('icon', { initialValue: '' })(
                                    <TreeSelect
                                        showSearch
                                        style={{ width: 300 }}
                                        dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                                        placeholder="Please select"
                                        allowClear
                                        treeDefaultExpandAll

                                    >
                                        {iconsTree}
                                    </TreeSelect>
                                )}
                            </FormItem>

                            <FormItem {...tailFormItemLayout}>
                                <Button type="primary" htmlType="submit" className='manager-menus-btn'></Button>
                                <PermissionContainer permission={["function_del"]}>
                                    <Popconfirm title="?" onConfirm={this.removeMenu}>
                                        <Button
                                            type="danger"
                                            disabled={!this.state.tempMenu.id}
                                            className="manager-menus-btn"
                                            icon="delete"
                                        >
                                            
                                    </Button>
                                    </Popconfirm>
                                </PermissionContainer>
                                <Button type="warning" htmlType="reset" className='manager-menus-btn' onClick={this.resetFields}></Button>
                            </FormItem>
                        </Form>
                    </Col>
                </Row>
            </div>
        )
    }
}

export default Form.create()(Menu);


